@import './../../../style/variables';

.LinkBtn {
  width: 180px;
  height: 60px;
  cursor: pointer;
  outline: none;
  border: none;

  &__link {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $dark;
    color: $white;
  }
}
